import { AuthService, LoginPayload } from "@/services/auth/auth.service"
import { useMutation } from "@tanstack/react-query"
import { toast } from "react-hot-toast";

export const useLogin = () => {
    return useMutation({
        mutationFn: (payload: LoginPayload) => AuthService.login(payload),
        onMutate: () => {
            toast.loading("ang ng nhp...");
            toast.dismiss(); // Dismiss the loading toast after mutation starts
        },
        onSuccess: async (data) => {
            localStorage.setItem("token", data.token);
            toast.success("ng nhp thnh cng!");
        },
        onError: (error) => {
            toast.error("ng nhp tht bi. Vui lng kim tra li thng tin.");
            console.error("Login error:", error);
        }
    })
}